<template>
<div class="w90p" style="margin:0 auto;height: 100%; overflow: auto; display: flex; flex-direction: column;">
    <div class="flexBetween mt48">
        <div>
            <div class="firstTitle ">观点分析</div>
            <div class="secondDesc">观点分析简介</div>
        </div>
        <Input v-model="page.name" search placeholder="搜索" @on-search="search" style="width:200px;" />
    </div>
    <div class="flexBox mt24" style="flex-wrap:wrap;overflow: auto;" @scroll="scrollEvent">
        <div v-for="(v, o) in list" :key="o" class="newsList" @click="toDetail(v)">
            <div class="f500 text_deal">{{ v.title || '无标题' }}</div>
            <!-- <div class="f500 op7 text_deal_2">{{ v.content }}</div> -->
            <div class="f12 op7 mt4 alignCenter">
                <span class="text_deal" style="flex:1;">{{ v.nickname }}</span>
                <span>{{ v.time | getDateDiff}}</span>
            </div>
        </div>
        <noData v-if="!page.loading" :list="list" style="flex-shrink: 0; width: 100%;"></noData>
        <loading v-if="page.loading"></loading>
    </div>
</div>
</template>
    
<script>

export default {
    name: '',
    props: {
    },
    components: {

    },
    data() {
        return {
            list: [],
            page: {
                "name": '',
                "current": 1,
                "size": 20,
                loading: false,
                finish: false,
            },
        }
    },
    mounted() {
        this.getList();
    },
    methods: {
        // 滚定监听
        scrollEvent(e) {
            let that=this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >=e.currentTarget.scrollHeight-20) {   //容差：20px
                console.log('滚动到底部');
                if(!that.page.finish && !that.page.loading){
                    that.page.current+=1;
                    that.getList();
                }
            }
        },
        search(){
            this.page.current=1;
            this.getList();
        },
        toDetail(v) {
            localStorage.setItem('comment_detail', JSON.stringify(v));
            this.$router.push(`/commentDetail`);
        },
        //根据接口获取
        async getList() {
            if (this.page.loading) {
                return;
            }
            let d = {
                // "name": this.page.name,
                // "type": this.page.type,
                // "area": 'beijing',
                "current": this.page.current,
                "size": this.page.size,
            };
            this.page.loading = true;
            let res = await this.$api.point_viewpoint_page(d);
            // let res = await this.$api.news_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if (d.current == 1) {
                this.list = list;
            } else {
                this.list = list.concat(this.list);
            }
            this.page.loading = false;
        },
    },
}
</script>
    
<style scoped lang='less'>
.newsList {
    cursor: pointer;
    width: calc((100% - (24px * 3)) / 4);
    padding: 15px;
    border-radius: 8px;
    margin: 0 24px 24px 0;
    // background: #2b313955;
}

.newsList:nth-child(4n) {
    margin: 0 0 24px 0;
}

.newsList:hover {
    background: rgba(56, 97, 251, 0.1);
}
:deep(.ivu-input-wrapper){
    padding: 5px 40px 5px 10px;
    background: #2B3139;
    border-radius: 8px;
}
:deep(body .ivu-input){
    background: #2B3139;
}
</style>